<template>
  <div class="box">
    <div class="bigbox">
      <div class="content">
        <h1>绘图应用</h1>
        <canvas class="acan" ref="canvas" width="500px" height="500px"></canvas>
      </div>
    </div>
    <div class="tool">
      <el-card>
        <input type="color" v-model="selectedColor" @input="changeColor" />
        <button @click="togglePen" class="iconfont icon-pen" style="font-size: 22px"></button>
        <button @click="toggleEraser" class="iconfont icon-rubber" style="font-size: 22px"></button>
        <button @click="clearCanvas" class="iconfont icon-clear" style="font-size: 22px"></button>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      isErasing: false, // 橡皮擦模式标志
      selectedColor: '#000000' // 选定的颜色，默认为黑色
    }
  },
  mounted() {
    // 获取canvas元素和上下文
    const canvas = this.$refs.canvas
    const context = canvas.getContext('2d')
    let isDrawing = false

    // 添加事件监听器
    canvas.addEventListener('mousedown', this.startDrawing)
    canvas.addEventListener('mousemove', this.draw)
    canvas.addEventListener('mouseup', this.stopDrawing)
    canvas.addEventListener('mouseout', this.stopDrawing)

    // 将canvas、context和isDrawing保存到组件实例中
    this.canvas = canvas
    this.context = context
    this.isDrawing = isDrawing
  },
  methods: {
    startDrawing(event) {
      this.isDrawing = true
      this.context.beginPath()
      this.draw(event)
    },
    draw(event) {
      if (!this.isDrawing) return

      const x = event.clientX - this.canvas.offsetLeft
      const y = event.clientY - this.canvas.offsetTop

      if (this.isErasing) {
        this.context.globalCompositeOperation = 'destination-out'
        this.context.lineWidth = 30 // 设置橡皮擦大小
      } else {
        this.context.globalCompositeOperation = 'source-over'
        this.context.lineWidth = 1 // 设置正常绘制线条的大小
        this.context.strokeStyle = this.selectedColor
      }

      this.context.lineTo(x, y)
      this.context.stroke()
    },
    stopDrawing() {
      this.isDrawing = false
    },
    togglePen() {
      this.isErasing = false
    },
    toggleEraser() {
      this.isErasing = true
    },
    changeColor() {
      this.context.strokeStyle = this.selectedColor
    },
    // 清屏功能
    clearCanvas() {
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
    }
  }
}
</script>

